<template>
	<view>
		<view class="banner" v-if="isShow">
			<!-- <u-image width="100%"  class="banner-img" height="300rpx" :src="dataForm.articleCover"></u-image> -->
			<image class="banner-img"  :src="dataForm.articleCover"></image>
			<view class="banner-title">{{dataForm.sharingName}}</view>
		</view>
		<view class="article-meta">
			<text class="article-author">{{dataForm.author}}</text>
			<text class="article-text" style="line-height: 42rpx;">发表于</text>
			<text class="article-time">{{dataForm.pubDate}}</text>
		</view>
		<!-- <view class="article-content">
			<u-read-more show-height="700" toggle>
				<rich-text :nodes="dataForm.sharingContent" style="white-space: pre-wrap"></rich-text>
			</u-read-more>
			
		</view> -->
		<view class="article-content" v-if="isShow">
			<web-view :src="url"></web-view>
			<!-- <u-read-more show-height="700" toggle>
				<rich-text :nodes="dataForm.cmsContent" style="white-space: pre-wrap"></rich-text>
			</u-read-more> -->
			
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				url:'',
				title: '',
				dataForm: {},
				sharingId:'',
				isShow:false,
			}
		},
		onLoad(obj) {
			console.log('objddddd',obj)
			this.sharingId = obj.id
			// uni.setNavigationBarTitle({
			// 	title: this.banner.title
			// });
			this.isShow = false
			this.getDetail();
		},

		
		methods: {
			getDetail() {
				this.request({
					url: "/banner/sharing",
					data:{
						sharingId:this.sharingId
					}
				}).then(data => {
					if (data && data.resphead && data.resphead.succeed) {
						this.dataForm = data.respbody
						this.isShow = true
						this.dataForm.pubDate =  moment(this.dataForm.pubDate).format('yyyy-MM-DD HH:mm:SS')
						console.log('dataForm',this.dataForm)
						if(this.dataForm.sharingType == 1){
							this.url = this.dataForm.sharingUrl
						}else if(this.dataForm.sharingType == 2){
							this.url = `/hybrid/html/pdf/local.html?type=1&url=${decodeURIComponent(this.dataForm.articlePdf)}`;
						}
						uni.setNavigationBarTitle({
							title: this.dataForm.sharingName || '文章阅览'
						});
					} else {
						this.isShow = false
						uni.showToast({
							title: data.resphead.message || "获取文章详情失败",
							icon: "none"
						});
					}
				});
			},
			escape2Html(str) {
			    var arrEntities = { 'lt': '<', 'gt': '>', 'nbsp': ' ', 'amp': '&', 'quot': '"' };
			    return str.replace(/&(lt|gt|nbsp|amp|quot);/ig, function (all, t) { return arrEntities[t]; })
			  },
		}
	}
</script>

<style lang="scss" scoped>
	.banner {
		height: 360rpx;
		overflow: hidden;
		position: relative;
		background-color: #ccc;
	}

	.banner-img {
		width: 100%;
	}

	.banner-title {
		overflow: hidden;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		font-size: 32rpx;
		font-weight: 400;
		line-height: 42rpx;
		white-space: pre-wrap;
		color: white;
		z-index: 11;
		padding: 30rpx;
		background: rgba($color: #000000, $alpha: 0.5);
	}

	.article-meta {
		padding: 20rpx 40rpx;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		color: gray;
	}

	.article-text {
		font-size: 26rpx;
		line-height: 50rpx;
		margin: 0 20rpx;
	}

	.article-author,
	.article-time {
		font-size: 30rpx;
	}

	.article-content {
		padding: 0 30rpx;
		overflow: hidden;
		font-size: 30rpx;
		margin-bottom: 30rpx;
	}
</style>
